import React, { createContext, useState, useContext, useEffect } from 'react';

// To mt Context mi
const AuthContext = createContext();

// To mt Provider cho Context
export const AuthProvider = ({ children }) => {
  const [isLoggedIn, setIsLoggedIn] = useState(() => {
    const savedIsLoggedIn = localStorage.getItem('isLoggedIn');
    return savedIsLoggedIn;
  });

  const login = () => {
    setIsLoggedIn(true);
    localStorage.setItem('isLoggedIn', 'true');
  };

  const logout = () => {
    setIsLoggedIn(false);
    localStorage.setItem('isLoggedIn', 'false');
    sessionStorage.clear();
    localStorage.clear();
    window.location.href = '/login';
  };

  const checkTokenValidity = () => {
    const token = localStorage.getItem('token');
    if (token) {
      const tokenExpiration = JSON.parse(atob(token.split('.')[1])).exp * 1000;
      const now = new Date().getTime();

      if (now >= tokenExpiration) {
        logout();
      } else {
        setTimeout(() => {
          alert('Phin ng nhp ca bn  ht hn');
          logout();
        }, tokenExpiration - now);
      }
    }
  };

  const handleVisibilityChange = () => {
    if (!document.hidden) {
      checkTokenValidity();
    }
  };

  useEffect(() => {
    // Kim tra token khi component mount
    checkTokenValidity();

    // ng k s kin visibilitychange
    document.addEventListener('visibilitychange', handleVisibilityChange);

    return () => {
      // Hy ng k s kin khi component unmount
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
  }, []);

  return (
    <AuthContext.Provider value={{ isLoggedIn, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

// To mt custom hook  s dng trong cc component
export const useAuth = () => useContext(AuthContext);
